.single-choice-drag {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    max-width: 100vw;
    overflow-x: hidden;
  }
  
  .drop-targets-container {
    display: flex;
    justify-content: space-around; /* 或者使用 space-between, flex-start, flex-end 根据需要调整 */
    align-items: center;
    margin-bottom: 20px; /* 为了与拖拽项分开 */
  }
  
  .drop-area {
    margin: 0 10px; /* 为每个目标区域添加一些间距 */
    width: 90%;
    max-width: 600px;
    position: relative;
  }
  
  .drop-area-content img {
    width: 100%;
    height: auto;
  }
  
  .dropped-item {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .dropped-item img {
    width: 30%;
    height: auto;
    object-fit: contain;
  }
  
  .items-container {
    display: flex;
    justify-content: space-between;
    width: 90%;
    max-width: 900px;
  }
  
  .draggable-item {
    width: 30%;
    cursor: move;
  }
  
  .draggable-item img {
    width: 100%;
    height: auto;
    object-fit: cover;
  }
  
  .draggable-item.dragging {
    opacity: 0.5;
  }
  
  .draggable-item.dropped {
    visibility: hidden;
  }
  
  .drag-layer {
    position: fixed;
    pointer-events: none;
    z-index: 100;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
  }
  
  .drag-preview {
    /* width: 27%;  */
    /* 30% of 90% container width */
    /* max-width: 270px;  */
    /* 30% of 900px max-width */
  }
  
  .drag-preview img {
    width: 100%;
    height: auto;
    object-fit: cover;
  }
  
  .result {
    margin-top: 20px;
    text-align: center;
  }
